"use client";

import React from "react";
import Image from "next/image";

interface CertificateProps {
  level: string;
  subtitle: string;
  description: string;
  details: string;
  completed: boolean;
  image: string;
  pdfPreview: string; // 🔹 Vista previa del PDF
}

const CertificateCard: React.FC<CertificateProps> = ({
  level,
  subtitle,
  description,
  details,
  completed,
  image,
  pdfPreview,
}) => {
  return (
    <div
      className={`relative border rounded-lg p-4 shadow-md ${
        completed ? "bg-white" : "bg-gray-200 opacity-70"
      }`}
    >
      {/* 🔹 Icono de bloqueo/desbloqueo según estado */}
      <div className="absolute top-4 right-4">
        <Image
          src={completed ? "/assets/icons/unlock.svg" : "/assets/icons/lock.svg"}
          alt={completed ? "Desbloqueado" : "Bloqueado"}
          width={24}
          height={24}
        />
      </div>

      {/* 🔹 Información del certificado */}
      <h2 className="text-xl font-semibold">{level}</h2>
      <p className="text-sm text-gray-600">{subtitle}</p>
      <p className="mt-2 font-bold">{description}</p>
      <p className="text-gray-500 text-sm">{details}</p>

      {/* 🔹 Vista previa del certificado (PDF) */}
      <div className="mt-4">
        {completed ? (
          <Image
            src={pdfPreview}
            alt={`Certificado de ${level}`}
            width={300}
            height={200}
            className="w-full h-auto"
          />
        ) : (
          <Image
            src={image}
            alt={level}
            width={300}
            height={200}
            className="w-full h-auto opacity-50"
          />
        )}
      </div>

      {/* 🔹 Botón de descargar certificado */}
      {completed && (
        <a
          href={`/certificates/download/${level}.pdf`}
          className="mt-4 block w-full bg-primary text-white py-2 rounded text-center"
          download
        >
          Descargar Certificado
        </a>
      )}

      {/* 🔹 Íconos de compartir */}
      {completed && (
        <div className="mt-4 flex justify-center gap-4">
          <Image src="/assets/icons/whatsapp.svg" alt="Compartir en WhatsApp" width={24} height={24} />
          <Image src="/assets/icons/facebook.svg" alt="Compartir en Facebook" width={24} height={24} />
          <Image src="/assets/icons/linkedin.svg" alt="Compartir en LinkedIn" width={24} height={24} />
        </div>
      )}
    </div>
  );
};

export default CertificateCard;
